import { domUtils } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export default function getAllRowsHeight(_, { node }: { node: Table }) {
  return node.nodes.map((tableRow) => {
    // eslint-disable-next-line react/no-find-dom-node
    const tr = domUtils.findDOMNode(tableRow.key) as HTMLElement;
    // 这里就应该取实际的，不应该取设置的。内容太高撑开的时候，取设置的加delta就是有问题的
    const h = tr.offsetHeight;
    return h;
  });
}
